<template>
  <div class="prediksi-bola" v-if="this.prediksiTipsData != null">
    <div
      class="prediksi-list"
      v-for="(items, index) in this.prediksiTipsData"
      :key="index"
    >
      <div class="tanggal-prediksi">
        {{ formatDateIndonesia(index) }}
      </div>
      <div class="prediksi-item" v-for="(item, indexy) in items" :key="indexy">
        <div class="liga abs-center">{{ item.ligainfo.name }}</div>
        <div class="waktu abs-center">
          {{ item.kickoff.split(" ")[1].slice(0, -3) }} WIB
        </div>
        <div class="playing-team">
          <span class="home_team">{{ item.home_team }}</span>
          <span class="vs"> VS </span>
          <span class="away_team">{{ item.away_team }}</span>
        </div>
        <div class="tips abs-center" v-html="item.prediksi_tips"></div>
        <div class="score abs-center">
          {{ item.home_score != "-" ? item.home_score : "" }} -
          {{ item.away_score != "-" ? item.away_score : "" }}
        </div>
        <div class="status abs-center">{{ item.status_tips }}</div>
      </div>
    </div>
  </div>
</template>


<style lang="scss" >
.prediksi-bola {
  display: flex;
  flex-direction: column;
  width: 100%;
  .prediksi-list {
    .tanggal-prediksi {
      font-weight: bolder;
    }
  }
  .prediksi-item {
    display: flex;
    flex-direction: row;
    background: #042f70;

    margin-top: 3px;
    color: #ffffff;
    align-items: center;
    height: 50px;
    flex-wrap: wrap;

    .liga {
      flex-basis: 20%;
      &:before {
        content: "\A0";
      }
    }
    .waktu {
      flex-basis: 10%;
      display: flex;
      height: 100%;

      margin: auto;
      background: #1858b9;
    }
    .playing-team {
      flex-basis: 30%;
      background: #1d3a66;
      align-items: center;
      display: flex;
      height: 100%;
      span.vs {
        &:before {
          content: "\A0";
        }
        &:after {
          content: "\A0";
        }
      }
      &:before {
        content: "\A0";
      }
    }
    .tips {
      flex-basis: 20%;
      background: #102443;
      display: flex;
      height: 100%;
      &:before {
        content: "\A0";
      }
    }
    .score {
      flex-basis: 10%;
      background: #045ca7;
      display: flex;
      height: 100%;
    }
    .status {
      flex-basis: 10%;
      background: #00060b;
      display: flex;
      height: 100%;
    }
  }
}
.abs-center {
  text-align: center;
  align-items: center;
  justify-content: center;
}
</style>


<script>
import axios from "axios";
import moment from "moment";
moment.locale("id");
export default {
  data() {
    return {
      prediksiTipsData: null,
    };
  },
  created() {
    this.getData();
    console.log(moment(Date.now()).fromNow());
  },
  methods: {
    formatDateIndonesia(date) {
      return moment(date).format("D MMMM YYYY");
    },
    getData() {
      var vm = this;
      axios
        .get("https://mafiaparlay.com/api/homepage-data/all")
        .then(function (resp) {
          if (resp.data.success == true) {
            vm.prediksiTipsData = resp.data.data;
          }
        })
        .catch(function (error) {
          console.log(error.data);
        });
    },
  },
};
</script>
